import React from "react";
import "./App.css";

class Addition extends React.Component {
  state = {
    num1: 1,
    num2: 1,
    score: 0,
    response: ""
  };

  render() {
    return (
      <div className="Add">
        <h1>The Addition Game</h1>
        <h2>
          {this.state.num1} + {this.state.num2}
        </h2>
        <input
          value={this.state.response}
          onChange={this.updateResponse}
          onKeyPress={this.inputKeyPress}
        />
        <div className="score">SCORE: {this.state.score}</div>
      </div>
    );
  }

  updateResponse = e => {
    this.setState({
      response: e.target.value //storing the entered value in response
    });
  };

  inputKeyPress = e => {
    if (e.key === "Enter") {
      //checking if the Enter key is pressed
      const answer = parseInt(this.state.response); // converting the str into int
      if (answer === this.state.num1 + this.state.num2) {
        // Answer is right
        // Notice the use of 'state' as the variable to update the previous state!
        this.setState(state => ({
          score: state.score + 1,
          // We are getting a random integer number btw 1 and 10
          num1: Math.ceil(Math.random() * 10),
          num2: Math.ceil(Math.random() * 10),
          response: "" // we are clearing the response input field
        }));
      } else {
        // Answer is wrong
        this.setState(state => ({
          response: "" // we are clearing the response input field
        }));
        alert("Sorry, but you are dead WRONG!");
      }
    }
  };
}

export default Addition;
